"use client";

import { useState, useEffect, useRef } from 'react';
import { useRouter, useSearchParams, usePathname } from 'next/navigation';
import Link from 'next/link';
import UserLayout from '@/components/user/UserLayout';
import useAuthStore from '@/store/authStore';
import { couponService } from '@/services/couponService';
import toast from 'react-hot-toast';
import Cookies from 'js-cookie';
import { BiChevronUp, BiChevronDown } from 'react-icons/bi';

export default function RedeemCouponPage() {
  const router = useRouter();
  const pathname = usePathname();
  const searchParams = useSearchParams();
  const { user, isAuthenticated, isLoading: authLoading, initializeAuth, getCurrentUser } = useAuthStore();
  const [isInitializing, setIsInitializing] = useState(true);
  const hasInitialized = useRef(false);
  const isUsedCoupons = searchParams.get('used') === 'true';

  // Monitor pathname changes to detect unauthorized redirects
  useEffect(() => {
    console.log("üîç PATHNAME MONITOR: Current pathname:", pathname);
    if (pathname !== '/redeem-coupon' && !pathname.startsWith('/redeem-coupon')) {
      console.error("üö® UNEXPECTED PATHNAME CHANGE! Current:", pathname, "Expected: /redeem-coupon");
      // If we're authenticated and somehow navigated away, redirect back
      if (isAuthenticated && pathname !== '/login') {
        console.log("üîÑ Redirecting back to /redeem-coupon");
        router.replace('/redeem-coupon');
      }
    }
  }, [pathname, isAuthenticated, router]);
  
  console.log("=== REDEEM COUPON PAGE RENDERED ===");
  console.log("isAuthenticated:", isAuthenticated);
  console.log("authLoading:", authLoading);
  console.log("isInitializing:", isInitializing);
  console.log("user:", user);
  console.log("Current pathname (from hook):", pathname);
  if (typeof window !== 'undefined') {
    console.log("Current window.location.pathname:", window.location.pathname);
  }
  
  const [coupons, setCoupons] = useState([]);
  const [isLoadingCoupons, setIsLoadingCoupons] = useState(false);
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    // Prevent multiple initializations
    if (hasInitialized.current) {
      console.log("Already initialized, skipping");
      return;
    }

    const checkAuth = async () => {
      hasInitialized.current = true;
      console.log("=== REDEEM COUPON: CHECKING AUTH ===");
      const token = Cookies.get('token');
      console.log("Token exists:", !!token);
      
      if (token) {
        console.log("Initializing auth...");
        initializeAuth();
        try {
          await getCurrentUser();
          console.log("getCurrentUser successful");
        } catch (error) {
          console.error("Failed to get current user:", error);
        }
      } else {
        console.log("No token found");
      }
      
      // Always set isInitializing to false after checking
      console.log("Setting isInitializing to false");
      setIsInitializing(false);
    };

    checkAuth();
  }, [initializeAuth, getCurrentUser]);

  useEffect(() => {
    console.log("=== REDEEM COUPON: AUTH CHECK EFFECT ===");
    console.log("isInitializing:", isInitializing);
    console.log("authLoading:", authLoading);
    console.log("isAuthenticated:", isAuthenticated);
    console.log("user:", !!user);
    console.log("Current window.location.pathname:", typeof window !== 'undefined' ? window.location.pathname : 'N/A');
    
    // Check if token exists in cookies - if token exists, wait for auth to load
    const token = typeof window !== 'undefined' ? Cookies.get('token') : null;
    console.log("Token exists:", !!token);
    
    // If token exists, DON'T redirect - wait for auth to initialize
    if (token) {
      console.log("‚úÖ Token found, waiting for auth to initialize - NO REDIRECT");
      return; // Don't redirect if token exists - auth is still loading
    }
    
    // ONLY redirect to login if:
    // 1. Initialization is complete
    // 2. Auth is not loading
    // 3. User is NOT authenticated
    // 4. No token exists
    // 5. No user data
    if (!isInitializing && !authLoading && !isAuthenticated && !user && !token) {
      console.log("‚ùå User not authenticated and no token, redirecting to login");
      toast.error('Please login to view coupons');
      router.push('/login?redirect=/redeem-coupon');
      return; // Exit early to prevent any other redirects
    }
    
    // If authenticated or user exists, do NOT redirect anywhere - stay on this page
    if (isAuthenticated || user) {
      console.log("‚úÖ User authenticated, staying on /redeem-coupon page - NO REDIRECT");
      // Explicitly check we're on the right page
      if (typeof window !== 'undefined' && window.location.pathname !== '/redeem-coupon') {
        console.log("‚ö†Ô∏è WARNING: Pathname mismatch! Current:", window.location.pathname, "Expected: /redeem-coupon");
      }
    }
  }, [isAuthenticated, authLoading, isInitializing, router, user]);

  useEffect(() => {
    const loadCoupons = async () => {
      if (!isAuthenticated || !user) return;
      
      try {
        setIsLoadingCoupons(true);
        const response = await couponService.getUserCoupons({ used: isUsedCoupons });
        setCoupons(response.data || []);
      } catch (error) {
        console.error('Failed to load coupons:', error);
        toast.error('Failed to load coupons');
      } finally {
        setIsLoadingCoupons(false);
      }
    };

    if (isAuthenticated && user) {
      loadCoupons();
    }
  }, [isAuthenticated, user, isUsedCoupons]);

  // Filter coupons
  const filteredCoupons = coupons.filter(coupon => {
    if (!searchQuery) return true;
    const query = searchQuery.toLowerCase();
    return (
      coupon.code?.toLowerCase().includes(query) ||
      coupon.title?.toLowerCase().includes(query) ||
      coupon.category?.toLowerCase().includes(query)
    );
  });

  // Pagination
  const totalPages = Math.ceil(filteredCoupons.length / entriesPerPage);
  const startIndex = (currentPage - 1) * entriesPerPage;
  const endIndex = startIndex + entriesPerPage;
  const currentCoupons = filteredCoupons.slice(startIndex, endIndex);

  // If we have user data, show content immediately (don't wait for isInitializing)
  // This prevents the page from getting stuck in loading state
  if (!user && isInitializing) {
    console.log("=== REDEEM COUPON: SHOWING LOADING (no user yet) ===");
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-t-4 border-b-4 border-green-500 mx-auto mb-4"></div>
          <p className="text-gray-600 text-lg">Loading...</p>
        </div>
      </div>
    );
  }

  // If not authenticated after a reasonable time, redirect to login
  if (!isInitializing && !authLoading && !isAuthenticated && !user) {
    console.log("=== REDEEM COUPON: NOT AUTHENTICATED, RETURNING NULL ===");
    return null; // The useEffect will handle redirect
  }

  // If we have user, show content even if isInitializing is still true
  if (user || isAuthenticated) {
    console.log("=== REDEEM COUPON: USER/IS_AUTHENTICATED TRUE, SHOWING CONTENT ===");
  }

  console.log("=== REDEEM COUPON: RENDERING PAGE CONTENT ===");
  console.log("Final check - pathname (from hook):", pathname);
  if (typeof window !== 'undefined') {
    console.log("Final check - window.location.pathname:", window.location.pathname);
  }
  
  return (
    <UserLayout>
      <div className="py-6">
        {/* Heading */}
        <div className="mb-6">
          <h1 className="text-3xl font-bold text-blue-900 mb-2">
            Coupon Generated Data
          </h1>
          <nav className="text-gray-500 text-sm">
            <ol className="flex space-x-2 items-center">
              <li>
                <Link href="/dashboard" className="hover:underline text-gray-600 hover:text-blue-600">
                  Home
                </Link>
              </li>
              <li className="text-gray-400">/</li>
              <li>
                <Link href="/redeem-coupon" className="hover:underline text-gray-600 hover:text-blue-600">
                  Coupon Data
                </Link>
              </li>
              <li className="text-gray-400">/</li>
              <li className="text-gray-700">All Coupons</li>
            </ol>
          </nav>
        </div>

        {/* Coupon Data Table Section */}
        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <h2 className="text-xl font-bold text-blue-900 mb-4">Coupon Data Table</h2>
          
          {/* Table Controls */}
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-2">
              <select
                value={entriesPerPage}
                onChange={(e) => {
                  setEntriesPerPage(Number(e.target.value));
                  setCurrentPage(1);
                }}
                className="px-3 py-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none"
              >
                <option value={10}>10</option>
                <option value={25}>25</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </select>
              <span className="text-sm text-gray-600">entries per page</span>
            </div>

            <div className="flex items-center gap-2">
              <input
                type="text"
                placeholder="Search..."
                value={searchQuery}
                onChange={(e) => {
                  setSearchQuery(e.target.value);
                  setCurrentPage(1);
                }}
                className="px-4 py-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none"
              />
            </div>
          </div>

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="bg-green-100">
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Coupon Code
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Coupon Amount
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Coupon For
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Creation Date
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Status
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  {isUsedCoupons && (
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                      <div className="flex items-center gap-1">
                        Used Date
                        <div className="flex flex-col">
                          <BiChevronUp className="text-xs text-gray-400" />
                          <BiChevronDown className="text-xs text-gray-400" />
                        </div>
                      </div>
                    </th>
                  )}
                </tr>
              </thead>
              <tbody>
                {isLoadingCoupons ? (
                  <tr>
                    <td colSpan={isUsedCoupons ? 6 : 5} className="px-4 py-8 text-center">
                      <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-green-500 mx-auto"></div>
                      <p className="text-gray-600 mt-2">Loading coupons...</p>
                    </td>
                  </tr>
                ) : currentCoupons.length > 0 ? (
                  currentCoupons.map((coupon, index) => (
                    <tr
                      key={coupon._id}
                      className={`border-b border-gray-100 ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}`}
                    >
                      <td className="px-4 py-3 text-sm text-gray-900 font-medium">
                        {coupon.code || 'N/A'}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {coupon.value?.isPercentage 
                          ? `${coupon.value.percentage}% OFF`
                          : `‚Çπ${coupon.value?.amount || 0}`
                        }
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {coupon.title || coupon.category || 'N/A'}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {coupon.createdAt 
                          ? new Date(coupon.createdAt).toLocaleDateString('en-GB', {
                              day: '2-digit',
                              month: '2-digit',
                              year: 'numeric'
                            })
                          : 'N/A'
                        }
                      </td>
                      <td className="px-4 py-3 text-sm">
                        <span className={`px-2 py-1 rounded-full text-xs font-semibold ${
                          coupon.isUsed || coupon.status === 'used'
                            ? 'bg-green-100 text-green-800'
                            : coupon.status === 'expired'
                            ? 'bg-red-100 text-red-800'
                            : 'bg-yellow-100 text-yellow-800'
                        }`}>
                          {coupon.isUsed || coupon.status === 'used' ? 'Used' : coupon.status || 'Unused'}
                        </span>
                      </td>
                      {isUsedCoupons && (
                        <td className="px-4 py-3 text-sm text-gray-700">
                          {coupon.usedAt || coupon.redeemedAt
                            ? new Date(coupon.usedAt || coupon.redeemedAt).toLocaleDateString('en-GB', {
                                day: '2-digit',
                                month: '2-digit',
                                year: 'numeric'
                              })
                            : 'N/A'
                          }
                        </td>
                      )}
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={isUsedCoupons ? 6 : 5} className="px-4 py-8 text-center text-gray-500">
                      No entries found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="flex items-center justify-between mt-4 pt-4 border-t border-gray-200">
              <div className="text-sm text-gray-600">
                Showing {startIndex + 1} to {Math.min(endIndex, filteredCoupons.length)} of {filteredCoupons.length} entries
              </div>
              <div className="flex items-center gap-2">
                <button
                  onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                  disabled={currentPage === 1}
                  className="px-3 py-1 border border-gray-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50"
                >
                  Previous
                </button>
                <span className="text-sm text-gray-600">
                  Page {currentPage} of {totalPages}
                </span>
                <button
                  onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                  disabled={currentPage === totalPages}
                  className="px-3 py-1 border border-gray-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50"
                >
                  Next
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </UserLayout>
  );
}









